import React, { useEffect, useState } from 'react';
import Link from 'next/link';
import { Paper, Grid, TableContainer, Table, TableCell, TableBody, TableRow, LinearProgress } from '@mui/material';
import { sendApiRequest } from '../../lib/api/public';
import { renderJobStatus } from '../utils';

const Jobs = ({ clientId, onChange }: { clientId: string, onChange?}) => {
  const [isLoading, setLoading] = useState(false);
  const [jobs, setJobs] = useState();

  const getJobs = () => {
    setLoading(true);

    sendApiRequest({
      path: `/clients/jobs?clientId=${clientId}`,
      method: 'GET',
    }).then((response: any) => {
      setLoading(false);

      if (!response.error) {
        setJobs(response);

        if (onChange) {
          onChange();
        }
      }
    })
  }

  useEffect(() => {
    getJobs();
  }, [clientId])

  const renderJobs = () => {
    if (isLoading) {
      return <LinearProgress />
    }

    return (
      <>
        <Grid container justifyContent="space-between" alignItems="center">
          <Grid item><h3>Jobs</h3></Grid>
        </Grid>

        <TableContainer component={Paper}>
          <Table>
            <TableBody>
              {(jobs || []).map((job) => (
                <TableRow
                  key={job._id}
                  sx={{ '&:last-child td, &:last-child th': { border: 0 } }}
                >
                  <TableCell>
                    <Link href={`job?id=${job._id}`}>
                      {job.title}
                    </Link>
                    <br />
                    {job.location}
                  </TableCell>
                  <TableCell>
                    {renderJobStatus(job)}
                  </TableCell>
                </TableRow>
              ))}
            </TableBody>
          </Table>
        </TableContainer>
      </>
    )
  }

  return (
    renderJobs()
  );
};

export default Jobs;
