import React, { useEffect, useState } from 'react';
import moment from 'moment';
import { useRouter } from 'next/router';
import AuthLayout from '../components/AuthLayout';
import { Paper, Grid, Button, TextField, Select, MenuItem } from '@mui/material';
import { readFile, sendApiRequest } from '../lib/api/public';
import Editor from '../components/common/Editor';
import Files from '../components/common/Files';
import Uploader from '../components/common/Uploader';
import notify from '../lib/notify';
import DetailItem, { fcSx } from 'components/common/DetailItem';
import { renderClientStatus, renderDetailItem } from 'components/utils';
import Jobs from '../components/clientDetail/Jobs';

type File = {
  name: string;
  url: string;
  date: Date;
}

const Company = () => {
  const router = useRouter();

  const [isLoading, setLoading] = useState(false);
  const [client, setClient] = useState<any>();

  const [logo, setLogoUrl] = useState('');
  const [name, setName] = useState('');
  const [websiteUrl, setWebsiteUrl] = useState('');
  const [companyEmailAddress, setCompanyEmailAddress] = useState('');
  const [notes, setNotes] = useState('');
  const [status, setStatus] = useState('');
  const [address, setAddress] = useState<any>({});

  const getCompany = () => {
    if (router.isReady) {
      const { query } = router;

      setLoading(true);

      sendApiRequest({
        path: `/clients/detail?_id=${query.id}`,
        method: 'GET',
      }).then((response: any) => {
        setLoading(false);

        if (!response.error) {
          setClient(response);

          setLogoUrl(response.logo);
          setStatus(response.status);
          setName(response.name);
          setWebsiteUrl(response.websiteUrl);
          setCompanyEmailAddress(response.companyEmailAddress);
          setAddress(response.address || {});

          setNotes(response.notes);
        }
      })
    }
  }

  const { id } = router.query;

  useEffect(() => {
    getCompany();
  }, [id])

  const updateField = (data, callback?) =>
    sendApiRequest({
      path: `/clients/update-field`,
      method: 'POST',
      data: {
        clientId: client._id,
        ...data,
      }
    }).then((response: any) => {
      if (!response.error) {
        getCompany();
        notify('Success');

        if (callback) {
          callback();
        }
      }
    })

  const updateSimpleField = (fieldName, value) =>
    updateField({
      type: 'update-simple-field',
      fieldName,
      value,
    });

  const addAttachment = (resume: File) => {
    updateField({
      type: 'add-attachment',
      value: resume,
    })
  }

  const removeAttchment = (attachment: File) => {
    updateField({
      type: 'remove-attachment',
      value: attachment,
    })
  }

  const updateNotes = () => {
    updateField({
      type: 'update-simple-field',
      fieldName: 'notes',
      value: notes,
    })
  }

  if (!client) {
    return <AuthLayout title={''} isLoading={isLoading}>{' '}</AuthLayout>
  }

  const paperStyle = { padding: '10px 20px', marginBottom: '20px', marginRight: '10px' }

  const renderPrimaryInfo = () => {
    return (
         <Paper sx={paperStyle}>
            <h3>Primary</h3>

            <br />
            
            <DetailItem
              fieldName="logo"
              label="Logo"
              value={( logo ? <img height={50} src={readFile(logo)} /> : null)}
              editControl={() =>
                <>
                  <Uploader
                    label='Upload'
                    showList={false}
                    multiple={false}
                    onChange={(result) => {
                      setLogoUrl(result.url);
                    }}
                  />

                  <br /><br />

                  {logo ? <img height={50} src={readFile(logo)} /> : null}
                </>
              }
              onSave={() =>
                updateSimpleField('logo', logo)
              }
            />

            <DetailItem
              fieldName="name"
              label="Name"
              value={client.name}
              editControl={() =>
                <TextField
                  sx={fcSx}
                  defaultValue={client.name}
                  onChange={(e) => {
                    setName(e.target.value)
                  }}
                />
              }
              onSave={() =>
                updateSimpleField('name', name)
              }
            />

            <DetailItem
              fieldName="websiteUrl"
              label="Website"
              value={client.websiteUrl}
              editControl={() =>
                <TextField
                  sx={fcSx}
                  defaultValue={client.websiteUrl}
                  onChange={(e) => {
                    setWebsiteUrl(e.target.value)
                  }}
                />
              }
              onSave={() =>
                updateSimpleField('websiteUrl', websiteUrl)
              }
            />

            <DetailItem
              fieldName="companyEmailAddress"
              label="Company email"
              value={client.companyEmailAddress}
              editControl={() =>
                <TextField
                  sx={fcSx}
                  defaultValue={client.companyEmailAddress}
                  onChange={(e) => {
                    setCompanyEmailAddress(e.target.value)
                  }}
                />
              }
              onSave={() =>
                updateSimpleField('companyEmailAddress', companyEmailAddress)
              }
            />

           <DetailItem
              fieldName="status"
              label="Status"
              value={renderClientStatus(client)}
              editControl={() => {
                return (
                    <Select
                        sx={fcSx}
                        labelId='status-select-label'
                        defaultValue={client.status}
                        onChange={(e) => setStatus(e.target.value)}
                    >
                        <MenuItem value="active">Active</MenuItem>
                        <MenuItem value="onHold">InActive/On Hold</MenuItem>
                    </Select>
              )
              }}
              onSave={() =>
                updateSimpleField('status', status)
              }
            />

           <DetailItem
              fieldName="address"
              label="Address"
              value={client.address ? (
                  <span>{client.address.state}, {client.address.city}</span>
                ) : null}
              editControl={() =>
                  <div>
                       <TextField
                            fullWidth
                            multiline
                            rows={2}
                            label="Street"
                            defaultValue={address.street}
                            onChange={(e) => {
                                address.street = e.target.value;
                                setAddress(address);
                            }}
                        /> <br /><br />

                        <TextField
                            fullWidth
                            label="City"
                            defaultValue={address.city}
                            onChange={(e) => {
                                address.city = e.target.value;
                                setAddress(address);
                            }}
                        /> <br /><br />

                        <TextField
                            fullWidth
                            label="State"
                            defaultValue={address.state}
                            onChange={(e) => {
                                address.state = e.target.value;
                                setAddress(address);
                            }}
                        /> <br /><br />

                        <TextField
                            fullWidth
                            label="Zip Code"
                            defaultValue={address.zipcode}
                            onChange={(e) => {
                                address.zipcode = e.target.value;
                                setAddress(address);
                            }}
                        /> <br /><br />
                  </div>
              }
              onSave={() => {
                return updateSimpleField('address', address);
              }}
            />

            {renderDetailItem('Created Date', moment(client.createdAt).format('MM/DD/YYYY'))}
          </Paper>
    )
  }

  const renderFiles = () => {
    return (
         <Paper sx={paperStyle}>
            <Files
              files={client.attachments}
              onDelete={(attachment) => {
                removeAttchment(attachment)
              }}
              rightSide={(
                <>
                  <Uploader
                    label='Add atachment'
                    showList={false}
                    multiple={false}
                    onChange={(result) => {
                      addAttachment(result);
                    }}
                  />
                </>
              )}
            />

          </Paper>
    )
  }

  return (
    <AuthLayout
      title={
        <Grid container>
          {logo ? <img style={{ borderRadius: '50px', height: '30px', marginRight: '10px' }} src={readFile(logo)} /> : null}
          {client.name}
        </Grid>
      }
      isLoading={isLoading}
    >
      <Grid container flexDirection="row">
        <Grid item flex={1}>
          {renderPrimaryInfo()}

          <Paper sx={{ ...paperStyle, height: '500px' }}>
            <Grid container justifyContent="space-between" alignItems="center">
              <Grid item><h3>Notes</h3></Grid>

              <Grid item>
                <Button
                  size='small'
                  onClick={() => {
                    updateNotes()
                  }}
                  variant="contained">
                  Save
                </Button>
              </Grid>
            </Grid>

            <Editor style={{ height: '300px' }} value={notes} onChange={(value) => setNotes(value)} />
          </Paper>
        </Grid>

        <Grid item flex={1}>
          <Paper sx={paperStyle}>
            <Jobs clientId={client._id} />
          </Paper>

          {renderFiles()}
        </Grid>
      </Grid>
 
    </AuthLayout>
  );
};

export default Company;
